* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	background: linear-gradient(
		0deg,
		rgba(250, 194, 110, 1) 13%,
		rgba(208, 67, 67, 1) 95%,
		rgba(208, 47, 47, 1) 100%
	);
}
.flex {
	display: flex;
	flex-direction: row;
}
main {
	max-width: 600px;
	margin: auto;
}
.weather-display-container {
	width: 500px;
	margin: auto;
}
.general-information-container {
	display: grid;
	grid-template-columns: 150px 150px;
	grid-template-rows: repeat(3, 100px);
	div {
		text-align: center;
	}
}
